.article-body * {
  max-width: 100%;
}

.article-body {
  line-height: 1.8;
}

.article-content h1,
h2,
h3,
h4,
h5,
h6,
b,
strong {
  font-weight: var(--font-weight-500);
}

.article-content li,
p {
  margin-block-start: 1.6em;
  margin-block-end: 1.6em;
}

/*  */
.article-body img {
  max-width: 100%; /*  100% */
  height: auto; /*  */
  border-radius: 4px;
}

/*video  */
.article-body video {
  border-radius: 4px;
}

/*  */
.article-content {
  color: var(--color-text-2);
}

/*  */
.article-title a {
  text-decoration: none;
  color: var(--color-text-1); /*  */
  font-weight: 500;
}

/*  */
.article-body a {
  transition: color 0.1s linear;
  text-decoration: none;
  color: rgb(var(--primary-6));
  border-bottom: 1px dotted rgb(var(--primary-6)); /*  */
}

.article-body a:hover {
  text-decoration: none;
  color: rgb(var(--primary-5));
  border-bottom: 1px dotted rgb(var(--primary-5)); /*  */
}

/*  */
.article-body blockquote {
  border-left: 1px solid var(--color-border-3);
  color: var(--color-text-3);
  margin: 1em 0;
  padding: 0 20px;
}

/*code  -  */
.article-content td {
  color: var(--color-text-3);
  font-size: 0.8em;
}

/*code  */
.article-content code {
  font-family: "JetBrains Mono", Consolas, Monaco, "Andale Mono", monospace;
  font-size: 0.8em;
  background-color: var(--color-neutral-3);
  color: var(--color-text-2);
  padding: 2px;
  border-radius: 2px;
}

/*code  */
.article-content figure {
  margin: 16px 0;
}

/*code  */
.article-content pre {
  background-color: var(--color-neutral-3);
  line-height: 1.2;
  padding: 10px;
  border-radius: 4px;
  overflow: auto;
}
